.player {
  width: 800px;
  float: left;
  text-align: center;
}
.lrc {
  width: 600px;
  height: 544px;
  float: left;
  border: 1px solid #999;
}
.btn {
  height: 300px;
  width: 300px;
  background-color: black;
  border-radius: 50%;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  display: block;
  margin-top: 40px;
  margin-bottom: 40px;
  position: relative;
}
.btn img {
  height: 200px;
  width: 200px;
  border-radius: 50%;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  position: absolute;
  top: 50px;
  left: 50px;
  z-index: 999;
  -webkit-animation: mymove 20s infinite linear;
          animation: mymove 20s infinite linear;
}
@-webkit-keyframes mymove {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes mymove {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
.control {
  width: 500px;
  margin-left: 160px;
  text-align: center;
  position: relative;
}
.control .play,
.control .pause,
.control .prev,
.control .next {
  width: 40px;
  height: 40px;
  margin: 30px 10px;
  cursor: pointer;
}



